import common from "../../common/common.js"
var serverUrl = common.serverUrl
export default{
	data(){
		return{
			// userId:"",
			// password:""
			status:true,
			username:'',
			password:'',
			telephone:'',
			code:'',
			codeTime:0
		};
	},
	methods:{
		appOAuthLogin(e){
			var logintype =e.currentTarget.dataset.logintype
			uni.login({
				provider:logintype,
				success: (loginRes) => {
					// 授权登录成功以后 获取用户的信息
					uni.getUserInfo({
						provider:logintype,
						success: (info) => {
							// console.log(JSON.stringify(info))
							var userInfo = info.userInfo
							var faceIcon = ""
							var nickName=""
							var thirdPartyId=""
							if(logintype=="weixin"){
								faceIcon = userInfo.avatarUrl
								nickName=userInfo.nickName
								thirdPartyId=userInfo.openId
							}else if(logintype=="qq"){
								 faceIcon = userInfo.figureurl_qq_2
								 nickName=userInfo.nickname
								 thirdPartyId=userInfo.openid
							}else if(logintype=="sinaweibo"){
								 faceIcon = userInfo.avatar_large
								 nickName=userInfo.nickName
								 thirdPartyId=userInfo.id
							}
							uni.request({
								url:serverUrl+'/users/thirdPartyLogin/'+logintype,
								data:{
									"faceIcon":faceIcon,
									"nickName": nickName,
									"thirdPartyId": thirdPartyId
								},
								method:"POST",
								success: (res) => {
									if(res.data.code==0){
									var userInfo ={}
										userInfo=res.data.data
										console.log(res.data.data)
										// 缓存的API  保存用户信息到全局的缓存中
										uni.setStorageSync("globalUser",userInfo)
										uni.switchTab({
											url:"../me/me"
										})
										uni.showToast({
											title:res.data.msg,
											duration:2000,
											image:"../../static/icos/xixi.png"
										})
									}else if(res.data.code==466){
										uni.showToast({
											title:res.data.msg,
											duration:2000,
											image:"../../static/icos/error1.jpg"
										})
									}
								}

							})
						}
					})
				}
			})
			var appOAuthLogin
		}
		,
		//实现在微信小程序的微信登录
		wxLogin(e){
				// 通过微信开放能力 获得微信用户的基本信息
				var userInfo = e.detail.userInfo
				console.log(userInfo)
				console.log(userInfo.avatarUrl)
				//实现微信登录
				uni.login({
					provider:"weixin",
					success: (loginResult) => {
					// 获得微信登录的code:授权码
					var code = loginResult.code
					uni.request({
						url:serverUrl+'/users/mpLogin/'+code,
						data:{
							"avatarUrl":userInfo.avatarUrl,
							"city": userInfo.city,
							"country": userInfo.country,
							"gender": userInfo.gender,
							"language": userInfo.country,
							"nickName": userInfo.nickName,
							"province": userInfo.province
						},
						method:"POST",
						success: (res) => {
							if(res.data.code==0){
							var userInfo ={}
								userInfo=res.data.data
								console.log(res.data.data)
								// 缓存的API  保存用户信息到全局的缓存中
								uni.setStorageSync("globalUser",userInfo)
								uni.navigateBack({delta:1})
								uni.showToast({
									title:res.data.msg,
									duration:2000,
									image:"../../static/icos/xixi.png"
								})
							}else if(res.data.code==466){
								uni.showToast({
									title:res.data.msg,
									duration:2000,
									image:"../../static/icos/error1.jpg"
								})
							}
						}
					})
					}
				})
		},
		formSubmit(e){
			var userId = e.detail.value.userId
			var password = e.detail.value.password
			uni.request({
				url:serverUrl+'/users/registerOrLogin',
				data:{
					"userId":userId,
					"password":password
				},
				method:"POST",
				success: (res) => {
					if(res.data.code==0){
						var userInfo ={}
						userInfo=res.data.data
						// console.log(res.data)
						// 缓存的API  保存用户信息到全局的缓存中
						uni.setStorageSync("globalUser",userInfo)
						uni.switchTab({
							url:"../me/me"
						})
					}else if(res.data.code==466){
						uni.showToast({
							title:res.data.msg,
							duration:2000,
							image:"../../static/icos/error1.jpg"
						})
					}

				}
			})
		},
		initForm(){
			this.username='',
			this.password='',
			this.telephone='',
			this.code=''
		},
		changeLogin(){
			this.initForm()
			this.status=!this.status
		}
	},


}
